body {
  font-family: sans-serif;
}

.td {
  text-align: center;
  height: 2em;
  width: 2em;
  padding: 8px;
  cursor: pointer;
  border-radius: 8px;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
}

.td.clicked {
  pointer-events: none;
  background: bisque;
}

.divTable {
  display: flex;
  justify-content: center;
}
